	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>-1-</strong>\n初夏の幻想郷。 まだ梅雨突入前にも関わらず真夏を感じさせる日が続く。 至って平凡な日常だったが、霊夢は極めて退屈していた。 実はここ数日、暇潰しのタネである魔理沙が神社にやってこないのだ。 普段は毎日のようにやってきたのに…退屈な日も、邪魔な日も。 今日も日が暮れたが、魔理沙は来なかった。 何度か魔理沙の家にも行ってみようとは思ったこともあったが、実は霊夢は今まで霧雨邸にお邪魔したことは無い。 と言うより、魔理沙が嫌がるのである。 明日から霧雨邸を探しに行こうかと部屋の行灯に火を灯した瞬間、霊夢は一瞬空間が歪んだような錯覚を覚えた。 霊夢は瞬時我を失ったが、しばらくして立ち上がると陰陽玉と玉串を手に取り神社を後にした。 前髪の隙間から僅かに見えた目はいつもの陽気少女のものではなく博麗の末裔の鋭い光が映りこんでいた。 幻想郷にルールと言うものは存在しないが、一つだけ、博麗の掟と言う物がある。 それは…博麗大結界を破壊した者は人妖関わらず消滅させなければならないと言うものである。</p>\n<h2>-2-</h2>\n<p>幻想郷の奥地に存在する魔法の森。 ここは常に禍々しい妖気が渦巻いているのだが今日の妖気は一段と鋭く激しく渦巻いていた。 もし妖力を全く持たない外界の人間がこの妖気に少しでも触れた瞬間、体の少しも残さず蒸発してしまうだろう。 その森のほぼ中心に位置する館に魔理沙はいた…が、今日はいつもの魔理沙とは大きくかけ離れていた。 清酒「水道水」をラッパ飲みしては空き瓶を目の前の紫のポスター目掛けて放り投げ、また次の瓶に手を出している。 その目は虚ろで半分光を失っていたが、そこから湧き出る妖気は半端でない殺意を持っていた。 実は先日、<a href=\"/tag/yakumo-family?order=ASC\">橙と藍を苛めてきた</a>ばかりで、そこまでは良かったのだが、紫に返り討ちにされてしまったのだ。 だがそこは負けず嫌いの魔理沙である。 いつか殺してやると思った瞬間、微かな振動と共に変な衝撃が体を突き抜ける。 …どうやらその機会は思ったより早く来たようだ。 今の衝撃はほぼ間違いなく博麗大結界に亀裂の入った影響だ。 そしてそんなことが出来るやつなんてこの幻想郷で2人しかいない。 だがその1人はそんなことするやつじゃない…となると。 つい、にやけてしまった魔理沙。 悔しがってても仕方が無い、と外界へ向け飛び立っていった。 ──そして二人は、神風となる。</p>","frontmatter":{"cover":null,"date":"2004-08-30T00:17:57+09:00","strDate":"2004/8/29","title":"ストーリー"}}},"pathContext":{"path":"/ja/blog/2004/08/30-ストーリー/","langKey":"ja"}}

